@import 'variables';
.onas-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  sbackground-color: pink;
  smargin-top: -300px;
  .background-image {
    img {
      filter: grayscale(100%); } }
  .o-mnie {
    width: 100%;
    margin: 100px 0px 100px 0px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    sbackground-color: pink;
    h1 {
      box-sizing: border-box;
      width: 100%;
      padding: 20px;
      color: #2b2928;
      text-align: center;
      sbackground-color: #fff; }
    .photo {
      width: 350px;
      height: 350px;
      sborder-radius: 50%;
      overflow: hidden;
      sbackground-color: blue;
      img {
        width: 100%;
        height: 100%; } }
    .desc {
      background-color: #2e8b57;
      background-color: white;

      color: grey;
      width: 500px;
      height: 333px;
      box-sizing: border-box;
      padding: 30px;
      padding-top: 10px;
      line-height: 1.5;
      h3 {
        margin-bottom: 10px;
        color: #2b2928; } } } }

@media ( max-width: 520px ) {
  .onas-container {
    .o-mnie {
      sflex-direction: column;
      .photo {
        width: 100%;
        height: unset;
        max-width: 350px; }
      .desc {
        padding: 10px;
        height: unset;
        width: 100%; } } } }

@media ( max-width: 399px ) {
  .onas-container {
    .arrow {
      display: none; }
    .background-img {
      .text-on-image {
        color: $colorAlmostWhite; } } } }
